import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {AuthService} from '../../services/auth.service';
import{Router} from '@angular/router';
import { FlashMessagesService } from 'ngx-flash-messages';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  name:String;
  username:String;
  email:String;
  password:String;

  constructor(private validateService:ValidateService,
  private flashMessage:FlashMessagesService,
  private authservice:AuthService,
  private router:Router
  ) { }

  ngOnInit() {
  }
onRegisterSubmit(){
const user ={
name:this.name,
username:this.username,
email:this.email,
password:this.password,
}
if(!this.validateService.validateRegister(user)){
    this.flashMessage.show('please fill all fields',{classes:['alert-danger'],timeout:3000});
  return false;
}

if(!this.validateService.validateEmail(user.email)){
  this.flashMessage.show('please fill email correctly',{classes:['alert-danger'],timeout:3000});
  return false;
}


this.authservice.registerUser(user).subscribe(data =>{
if(data.sucess){
this.flashMessage.show('you are registered',{classes:['alert-success'],timeout:3000});
this.router.navigate(['/login']);
}else{
this.flashMessage.show('somthing wrong',{classes:['alert-danger'],timeout:3000});
this.router.navigate(['/register']);
}
})
}
}



